h1 {
  font-family: 'Product Sans Bold';
  color: #fff; }

h2, h3, h4 {
  color: #fff;
  font-family: 'Product Sans Regular';
  line-height: 1.5; }

.title-container {
  margin: 16rem 0 0 3em; }

.title {
  margin: .15em 0;
  padding: 0;
  font-size: 60px;
  font-weight: 200; }

.subtitle {
  display: -ms-flexbox;
  display: flex; }
  .subtitle a {
    margin: auto 3px; }
  .subtitle img {
    height: 35px;
    width: 40px; }
  .subtitle h4 {
    margin: 0px 3px 0px 0px;
    padding: 3px 8px;
    font-size: 30px;
    font-weight: 200;
    background: rgba(255, 255, 255, 0.2); }



